export default Footer;

const FooterContainer = () => {
    return (
        <Router>
            <div className={s.telegram}>

                <nav>
                    <ul>
                        <li>
                            <img className={s.img}
                                 src={"https://telegramm.net/telegramm/uploads/posts/2019-07/1562387760_paper-planes-3128885_960_720.png"}/>
                            <Link to="https://t.me/joinchat/AAAAAEZhiyOiE4QDpXkc7A">НАЖМИТЕ ЗДЕСЬ ЧТО БЫ СДЕЛАТЬ ЗАКАЗ В
                                ТЕЛЕГРАММ
                                <img className={s.img}
                                     src={"https://telegramm.net/telegramm/uploads/posts/2019-07/1562387760_paper-planes-3128885_960_720.png"}/>
                            </Link>
                        </li>
                    </ul>
                </nav>
            </div>
        </Router>
    );
}
